<template>
  <div class="app">
    <v-header :seller="seller"></v-header>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评价</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>
<script>
import aheader from './components/header/header';
export default {
  data() {
    return {
      seller: {}
    };
  },
  created() {
    let data = require('../data.json');
    this.seller = data.seller;
  },
  components: {
    'v-header': aheader
  }
};

</script>
<style>
@import url('./common/css/border-1px.css');

.tab {
  display: flex;
  width: 100%;
  height: 40px;
}

.tab-item {
  font-size: 14px;
  line-height: 40px;
  flex: 1;
  text-align: center;
}

.router-link-active {
  color: rgb(240, 20, 20);
}

</style>



